import React from 'react'
import Fade from 'react-reveal/Fade'

export default () => {
  return (
    <footer className='bck_red'>
        <Fade delay={500}>
            <div className='font_righteous footer_logo_veue'>
                The Venue
            </div>
            <div className='footer_copyright'>
            The Venue 2018. All rights reserved
            </div>
        </Fade>      
    </footer>
  )
}



